@import '../utils/variables';

body {

    color: $primary-text-color;
    @include font(400, 16px, 28px);
    font-family: 'Montserrat', 'Seymour One', sans-serif;

  }

  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  p {
    margin: 0;
  }
  
  img {
    display: block;
    max-width: 100%;
    height: auto;
  }
  
  address {
    font-style: normal;
  }
  
  button {
    display: block;
    cursor: pointer;
    border: none;
    font-family: inherit;
  }  
  
  ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  a {
    text-decoration: none;
  }
