.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto; 
  padding: 0 16px;
}

@media screen and (max-width: 479px) {
  .container {
    max-width: 479px;
  }
}
@media screen and (min-width: 480px) {
  .container {
    width: 480px;
    padding-left: 20px;
    padding-right: 20px;
  }
}
@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}
@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
    padding-left: 16px;
    padding-right: 16px;
  }
}
